.navbar {
  width: 100%;
  height: 50px;
  background-color: rgb(35, 49, 61);
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  z-index: 4;
  .icon {
    position: relative;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    border: 2px solid transparent;
    transition: 0.3s;
    cursor: pointer;
    &.on {
      i {
        color: rgb(104, 200, 255);
      }
    }
    &:hover {
      border: 2px solid rgba(255, 255, 255, 0.685);
    }
    p {
      position: absolute;
      top: -15px;
      right: -5px;
      width: 20px;
      height: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 13px;
      font-weight: 600;
      border-radius: 50%;
      color: #fff;
      background-color: red;
    }
    i {
      font-size: 140%;
      color: #fff;
      margin: 10px;
    }
  }
  .left {
    display: flex;
    align-items: center;
    width: 33%;
    a {
      margin: 0;
      width: fit-content;
      text-decoration: none;
      h1 {
        display: inline;
        color: #fff;
        font-size: 30px;
        margin-left: 20px;
      }
    }
  }
  .center {
    width: 33%;
    box-sizing: content-box;
    display: flex;
    align-items: center;
    input {
      width: 90%;
      height: 40px;
      padding-left: 10px;
      outline: none;
      border: none;
      border-radius: 4px 0 0 4px;
    }
    .disabled-link {
      pointer-events: none;
    }
    a {
      cursor: pointer;
      button {
        height: 40px;
        width: 60px;
        outline: none;
        border: none;
        border-radius: 0 4px 4px 0;
        background-color: rgb(255, 255, 255);
        transition: 0.2s;
        cursor: pointer;
        i {
          font-size: 20px;
        }
        &:hover {
          background-color: rgb(209, 234, 255);
        }
      }
    }
  }
  .right {
    width: 33%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .user-image {
      width: 40px;
      aspect-ratio: 1/1;
      border-radius: 50%;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: 10px;
      cursor: pointer;
      transition: 0.3s;
      &:hover {
        filter: brightness(1.2);
      }
      img {
        width: 100%;
      }
    }
  }
  .menu {
    position: absolute;
    top: 50px;
    left: -250px;
    width: 250px;
    height: 100vh;
    background-color: rgb(35, 49, 61);
    color: #fff;
    transition: 1s;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.156);
    overflow: hidden;
    &.show-menu {
      left: 0;
    }
    h3 {
      text-align: center;
    }
    ul {
      border-top: 2px solid rgba(221, 221, 221, 0.158);
      list-style: none;
      padding: 0;
      margin: 0;
      width: 100%;
      li {
        width: 100%;
        margin-bottom: 1px;
        .menu-item {
          position: relative;
          text-decoration: none;
          padding: 15px 20px;
          color: #fff;
          display: flex;
          align-items: center;

          transition: 0.4s;
          &::before {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            left: -250px;
            background-color: rgba(209, 231, 255, 0.199);
            border-top-right-radius: 40px;
            border-bottom-right-radius: 40px;
            transition: 0.4s;
            z-index: 0;
          }
          &:hover {
            &::before {
              left: -10px;
            }
          }
          .icon {
            width: 20px;
            height: 10px;
            margin-right: 15px;
            i {
              font-size: 18px;
              z-index: 1;
              color: rgb(103, 169, 255);
            }
          }
          p {
            z-index: 1;
            margin: 0;
            font-weight: 700;
          }
        }
      }
    }
  }
  .user-menu {
    position: absolute;
    width: 300px;
    height: 400px;
    right: 0;
    top: 50px;
    scale: 0;
    opacity: 0;
    transform-origin: top right;
    border-bottom-left-radius: 10px;
    background-color: #fff;
    transition: 0.5s;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 3;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.158);
    &.show-user-menu {
      scale: 1;
      opacity: 1;
    }
    .user-image {
      width: 150px;
      aspect-ratio: 1/1;
      overflow: hidden;
      border-radius: 50%;
      margin: 20px;
      margin-bottom: 0;
      img {
        width: 100%;
      }
    }
    .view-profile-btn {
      text-decoration: none;
      padding: 5px 10px;
      border-radius: 5px;
      color: #fff;
      font-weight: 600;
      font-size: 14px;
      transition: 0.2s;
      border: 2px solid transparent;
      background-color: rgb(0, 81, 255);
      &:hover {
        color: rgb(0, 81, 255);
        border: 2px solid rgb(0, 81, 255);
        background-color: rgb(255, 255, 255);
      }
    }
  }
  .notification {
    position: absolute;
    top: 50px;
    right: -300px;
    width: 300px;
    height: 100px;
    z-index: -1;
    background-color: #fff;
    transition: 1s;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    font-weight: 600;
    border-bottom-left-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.256);
    &.show-notification {
      right: 0;
    }
  }
  .cart {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    right: 0;
    top: 50px;
    width: 300px;
    padding: 10px;
    gap: 10px;
    background-color: rgb(35, 49, 61);
    border-bottom-left-radius: 10px;
    translate: 300px;
    transition: 1s;
    overflow: hidden;
    &.showc-cart {
      translate: 0;
    }
    .cart-is-empty {
      color: #fff;
    }
    .product-in-cart {
      position: relative;
      width: 100%;
      display: flex;
      background-color: rgba(255, 255, 255, 0.267);
      color: #fff;
      overflow: hidden;
      border-radius: 5px;
      padding: 10px 0;
      height: auto;
      transform: scaleY(1);
      transform-origin: top;
      transition: 0.4s;
      animation: card-item-show 0.7s;
      @keyframes card-item-show {
        0% {
          top: -100px;
          transform: scale(0);
        }
        100% {
          top: 0;
          transform: scale(1);
        }
      }
      .left {
        width: 30%;
        background-color: #fff;
        margin-left: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 5px;
        overflow: hidden;
        img {
          width: 80%;
        }
      }
      .right {
        margin-left: 10px;
        width: 70%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        .title {
          margin: 5px;
          font-size: 15px;
          width: 100%;
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
        }
        p {
          margin: 2px;
          width: 100%;
          display: flex;
          justify-content: space-between;
          padding-right: 10px;
          .remove {
            border-radius: 3px;
            border: 2px solid #fff;
            width: 65px;
            font-weight: 600;
            cursor: pointer;
            transition: 0.3s;
            &:hover {
              color: #fff;
              background-color: transparent;
            }
          }
          .change-buttons {
            display: flex;
            gap: 5px;
            button {
              width: 30px;
              border: 2px solid #fff;
              border-radius: 3px;
              font-weight: 600;
              font-size: 15px;
              transition: 0.3s;
              cursor: pointer;
              &:hover {
                color: #fff;
                background-color: transparent;
              }
            }
          }
        }
      }
    }
    .total {
      margin: 5px;
      color: #fff;
    }
    .buttons {
      width: 100%;
      display: flex;
      justify-content: space-between;
      button {
        padding: 10px 25px;
        border: none;
        outline: none;
        border-radius: 5px;
        color: rgba(255, 255, 255, 0.685);
        background-color: rgb(35, 49, 61);
        border: rgba(255, 255, 255, 0.589) 3px solid;
        font-weight: 600;
        font-size: 16px;
        cursor: pointer;
      }
    }
  }
}
