* {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: "Nunito Sans", sans-serif;
  overflow-x: hidden;
}
.rating {
  margin-bottom: 5px;
  .stars {
    i {
      color: rgb(255, 153, 0);
    }
  }
}

.section {
  width: 100vw;
  position: relative;
  background-color: rgb(231, 242, 255);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  padding: 20px;
  margin-bottom: 40px;
  overflow: hidden;
  .arrow {
    position: absolute;
    top: 50%;
    translate: 0 -50%;
    width: 50px;
    aspect-ratio: 1/1;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.274);
    font-size: 30px;
    color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    transition: 0.3s;
    z-index: 2;
    cursor: pointer;
    &:hover {
      scale: 1.1;
    }
    &::selection {
      background-color: transparent;
    }
  }
  .left-arrow {
    left: 50px;
  }
  .right-arrow {
    right: 50px;
  }
  .section-title {
    padding-left: 20px;
    font-weight: 600;
    font-size: 30px;
  }
  .products-line {
    width: 100vw;
    padding: 30px;
    display: flex;
    align-items: center;
    gap: 20px;
    transition: 0.7s;
    .product {
      width: 300px;
      min-width: 300px;
      height: 450px;
      border: solid #fff 2px;
      display: flex;
      flex-direction: column;
      border-radius: 10px;
      overflow: hidden;
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.237);
      .top {
        width: 100%;
        height: 50%;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #fff;
        img {
          width: 70%;
          transition: 0.6s;
          &:hover {
            scale: 1.1;
          }
        }
      }
      .bottom {
        position: relative;
        background-color: #fff;
        height: 50%;
        padding: 20px;
        .title-view {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: -5px;
          .title {
            text-align: start;
            font-size: 20px;
            overflow: hidden;
            width: 200px;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
          }
          a {
            background-color: rgb(0, 110, 255);
            padding: 5px 15px;
            border: 2px solid #fff;
            border-radius: 5px;
            font-weight: 600;
            cursor: pointer;
            transition: 0.3s;
            text-decoration: none;
            color: #fff;
            &:hover {
              background-color: #fff;
              border: 2px solid rgb(0, 110, 255);
              color: rgb(0, 110, 255);
            }
          }
        }
        .description {
          margin: 0;
          margin-bottom: 70px;
          font-size: 15px;
          opacity: 0.8;
          line-height: 18px;
          overflow: hidden;
          width: 100%;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
        }
        .price-count {
          width: 100%;
          position: absolute;
          bottom: 0;
          left: 50%;
          translate: -50%;
          display: flex;
          justify-content: space-evenly;
          .price {
            font-weight: 600;
            color: rgb(0, 88, 202);
          }
          .count {
            font-weight: 600;
            &.high {
              color: green;
            }
            &.low {
              color: rgb(255, 86, 86);
            }
          }
        }
      }
    }
  }
}
